import Vue from 'vue'
import Router from 'vue-router'

import Home from './pages/home/Home.vue'
import Skills from './pages/skills/Skills.vue'
import Work from './pages/work/Work.vue'

Vue.use(Router)

const router = new Router({
	routes: [
		{
			path: '/',
			name: 'home',
			component: Home
		},
		{
			path: '/skills',
			name: 'skills',
			component: Skills
		},
		{
			path: '/work',
			name: 'work',
			component: Work
		}
	]
})

export default router
